<template>
  <v-app>
    <v-container>
      <SiteHeader />
      <PlaceSelector place="Tekirdağ" @coords-available="coordsAvailable"/>
      <WeatherTabs v-if="coordsAreNotEmpty" :coords="{ lat: lat, lon: lon }"/>
    </v-container>
  </v-app>
  
</template>

<script setup>
import { ref } from "vue"

import PlaceSelector from './components/PlaceSelector.vue';
import SiteHeader from './components/SiteHeader.vue';
import WeatherTabs from './components/WeatherTabs.vue'

const lat = ref(0);
const lon = ref(0);
const coordsAreNotEmpty = ref(false);
function coordsAvailable(coords) {
  lat.value = coords.lat.value
  lon.value = coords.lon.value
  coordsAreNotEmpty.value = true;
}
</script>