export default function get7Days() {
    let dayNames = [];
    let today = new Date();

    for (let i = 0; i < 7; i++) {
        let currentDay = new Date(today);
        currentDay.setDate(today.getDate() + i);
        let dayName = currentDay.toLocaleDateString(undefined, { weekday: 'long' });
        dayNames.push(dayName);
    }

    return dayNames;
}
