<template>
<v-card class="mt-10">
    <v-card-title>Mevcut konum: {{ currentPlaceName }} <span class="text-disabled">({{ lat + ", " + lon }})</span></v-card-title>
    <v-text-field label="Konum ara..." v-model="placeInput" class="pa-5"></v-text-field>
    <v-list>
        <v-list-item
            v-for="result in searchResults" :key="result.id"
            @click="currentPlaceID = result.id"
            >
            <v-icon icon="mdi-map-marker" class="mr-2"></v-icon>{{ result.name }} <span class="text-disabled">{{ result.country }}</span>
        </v-list-item>
    </v-list>
</v-card>
</template>
    
<script setup>
import { ref, watch, defineProps, defineEmits, toRefs } from 'vue';
import searchPlace from '../services/searchPlace.js'

const emit = defineEmits(["coords-available"])

// Initial search
const lat = ref(0)
const lon = ref(0)
const props = defineProps({
    place: {
        type: String,
    }
});
const { place } = toRefs(props);
searchPlace(place.value).then(data => {
    let firstResult = data[0]
    lat.value = firstResult.latitude
    lon.value = firstResult.longitude
    emit("coords-available", {lat: lat, lon: lon})
})

// Typing
const searchResults = ref([])
const placeInput = ref('')
let typingTimeout = null
watch(placeInput, (newValue) => {
  if (typingTimeout) {
    clearTimeout(typingTimeout);
  }
  typingTimeout = setTimeout(() => {
    searchPlace(newValue).then(data => {
        searchResults.value = data;
    })
}, 500);
});

// New place selected
const currentPlaceName = ref(place.value);
const currentPlaceID   = ref(0)
watch(currentPlaceID, (newValue) => {
    searchPlace(newValue).then(data => {
        // search the place object in results
        let selectedPlace = searchResults.value.find((result) => {
            if (result.id == newValue) {
                return true
            }
        })
        currentPlaceName.value = selectedPlace.name
        lat.value = selectedPlace.latitude
        lon.value = selectedPlace.longitude
        emit("coords-available", {lat: lat, lon: lon})
        searchResults.value = []
    })
})

</script>
